.MainContainer {
  height: 100vh;
  background-color: #424632;
}

.container {
  justify-content: space-between;
  align-items: center;
}

.photoContainer {
  width: 270px;
  height: 400px;
  background-color: #eae6ca;
  transform: rotate(-20deg);
}

.frame {
  width: 284px;
  height: 420px;
  border: 8px solid #999950;
  /* border: 8px solid #a5c32e;  */
  display: flex;
  justify-content: center;
  align-items: center;
}

.textContainer {
  width: 300px;
  color: #eae6ca;
  border: 4px solid #999950;
  padding: 10px;
  font-family: 'Beer money';
  position: relative;
}
.textContainer::after{
  content: '';
  width: 50px;
  height: 50px;
  border: 5px solid #eae6ca;
  position: absolute;
top: -30px;
right: -30px;

}
.textContainer span{
  font-size: 27px;
}
.textContainer h1{
  font-size: 45px;
}

.photo {
  width: 270px;
  height: 400px;
  background-image: url('me.jpg');
  background-size: cover;
  background-position: center;
  transform: rotate(20deg);
}

@media screen and (max-width: 400px){
  .photoContainer{
    transform: rotate(-8deg);
  } 
  .photo{
    transform: rotate(8deg);
  }
  .textContainer::after{
    right: -20px;
  }
}